#statistics {
    background-image: url('../../assets/image/statisticsBg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
}


#statistics::after {
    content:'';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: linear-gradient(0deg, rgba(245,245,245,1) 0%, rgba(255,255,255,0.84) 100%);

    z-index: 1;

}

.statistics-row {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0;

    position: relative;
    z-index: 2;
}

.statistics__box {
    padding: 50px;
    text-align: center;
}

.statistics-row .statistics-col-4{
    border-right: 2px solid  rgba(13,135,40,0.3);
}

.statistics-row .statistics-col-4:last-child {
    border-right: none;
}

.statistics__box p {
    font-size: 40px;
    font-weight: 600;
    color: var(--Green);
}

.statistics__box span {
    font-size: 22px;
    color: #696969;
}
